.Header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-top: solid rgb(44, 44, 44) 6vh;
  max-width: 100vw;
}

.Header__logo {
  height: 12vh;
  margin: 0 2vw;
}

.Header__buttons {
  display: flex;
  flex-direction: row;
  margin: 0 1vw;
}
